/* Movie.css */

.movie-page{
    /*display: flex;*/
    display: block;
    -ms-flex-direction: column;
        flex-direction: column;
}

.movie-page h5{
    color: #888;
    font-weight: normal;
    line-height: 1.25rem;
}

.movie-page h5 span{
    font-size: inherit;
    font-weight: normal;
    color: #000;
    padding-left: 1rem;
}

.movie-page h4{
    margin: 0;
    font-size: 1.25rem;
    line-height: 2;
}

.movie-page .movie-backdrop{
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
    height: 575px;
    background-size: cover;
    background-position: center center;
}

.movie-page .movie-details{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    max-width: 800px;
    margin: 20px auto 60px auto;
}

.movie-page .movie-details h1{
    line-height: 1.5rem;
}

.movie-page .movie-details h1 span{
    font-size: inherit;
    font-weight: normal;
    padding-left: 1rem;
    color: #bbb;
    line-height: inherit;
}

.movie-page .genres{
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 1rem;
}

.movie-page .genres span{
    font-weight: normal;
}

.movie-page .separator{
    color: lightgray;
    padding: 0 10px;
}


