.search-button{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.search-button button{
    padding: 10px 21px;
    background: #1485CC;
    color: rgba(225,225,225,0.75);
    font-size: 1rem;
    cursor: pointer;
    -webkit-transition: color 0.2s ease-out;
    -o-transition: color 0.2s ease-out;
    transition: color 0.2s ease-out;
    outline: 0;
    border: 0;
}

.search-button button:hover{
    color: #fff;
}


